<template>
  <Teleport to="body" :disabled="!expanded">
    <div ref="main" class="expandable-image" :class="{ 'expanded': expanded }" @click="click" @touchMove="freezeVp">
      <i v-if="expanded && loaded" class="btn-close" @click="closeImage">
        <svg viewBox="0 0 24 24">
          <path fill="#666666" d="M19,6.41L17.59,5L12,10.59L6.41,5L5,6.41L10.59,12L5,17.59L6.41,19L12,13.41L17.59,19L19,17.59L13.41,12L19,6.41Z" />
        </svg>
      </i>
      <i v-else-if="loaded" class="btn-expand">
        <svg viewBox="0 0 24 24">
          <path fill="#000000" d="M10,21V19H6.41L10.91,14.5L9.5,13.09L5,17.59V14H3V21H10M14.5,10.91L19,6.41V10H21V3H14V5H17.59L13.09,9.5L14.5,10.91Z" />
        </svg>
      </i>
      <img ref="image" v-bind="$attrs" :class="imageClasses" loading="lazy" @load="onload" />
      <span v-if="title && loaded" class="title">{{ title }}</span>
    </div>
  </Teleport>
</template>

<script setup>
  import { ref, computed, defineProps, defineEmits, onMounted, nextTick } from 'vue';

  const props = defineProps({
    title: {
      type: String,
      default: '' 
    },
    dismissableMask: {
      type: Boolean,
      default: false
    },
    closeOnEscape: {
      type: Boolean,
      default: false
    },
    classes: {
      type: [String, Object],
      default: ''
    }
  });

  const emit = defineEmits(['loaded']);

  const main = ref();
  const image = ref();
  
  const timer = ref();
  const loaded = ref(false);
  const expanded = ref(false);

  const imageClasses = computed(() => {
    if (!loaded.value) {
      return props.classes;
    } else {
      // alert('loaded');
      return props.classes.concat('loaded');
    }
  });

  function onload() {
    clearInterval(timer.value);
    loaded.value = true;
    nextTick(() => {
      emit('loaded');
    });
  }

  function getRenderedSize(cWidth, cHeight, oWidth, oHeight) {
    const oRatio = oWidth > oHeight ? (oWidth / oHeight) : (oHeight / oWidth);
    const width = oWidth >= oHeight ? (oRatio * cHeight) : cWidth;
    const height = oHeight > oWidth ? (oRatio * cWidth) : cHeight;
    const left = (main.value.clientWidth - width) / 2;
    const right = left + width;
    const top = (main.value.clientHeight - height) / 2;
    const bottom = top + height;
    return { left, top, right, bottom };
  }

  function esc(event) {
    if (event.key === 'Escape') {
      closeImage(event);
    }
  }

  function click(event) {
    if (expanded.value) {
      if (props.dismissableMask) {
        onExpandedImageClick(event);
      }
    } else {
      expanded.value = true;
      
      if (props.closeOnEscape) {
        window.addEventListener('keydown', esc);
      }
    }
  }

  function closeImage(event) {
    expanded.value = false;
    window.removeEventListener('keydown', esc);
    event.stopPropagation();
  }

  function freezeVp(event) {
    if (expanded.value) {
      event.preventDefault();
    }
  }

  function onExpandedImageClick(event) {
    event.stopPropagation();
    const imagePosition = getRenderedSize(image.value.width, image.value.height, image.value.naturalWidth, image.value.naturalHeight);
    if ((event.clientX < imagePosition.left) || (event.clientX > imagePosition.right) || (event.clientY < imagePosition.top) || (event.clientY > imagePosition.bottom)) {
      expanded.value = false;
    }
  }

  onMounted(() => {
    if (!image.value.complete) {
      timer.value = setInterval(() => {
        if (image.value.complete) {
          loaded.value = true;
          nextTick(() => {
            emit('loaded');
          });
          clearInterval(timer.value);
        }
      }, 100);
    } else {
      loaded.value = true;
      nextTick(() => {
        emit('loaded');
      });
    }
  });
</script>

<style lang="scss" scoped>
  .expandable-image {
    position: relative;
    transition: 0.25s opacity;
    cursor: zoom-in;
    
    img {
      width: 100%;
      border-radius: 0.5em;
      position: relative;

      //&:not(.loaded) {
      //  display: none;
      //}
    }
    
    .title {
      width: 85%;
      background: rgba(0, 0, 0, 0.85);
      padding: 0.25em 0.5em;
      border-top-left-radius: 0.3em;
      border-top-right-radius: 0.3em;
      font-size: 0.9em;
      text-align: center;
      position: absolute;
      bottom: 0.25em;
      left: 50%;
      z-index: 1;
      transform: translateX(-50%);
      color: white;
    }

    svg {
      width: 2em;
      height: 2em;
      filter: drop-shadow(1px 1px 1px rgba(0,0,0,0.5));

      path {
        fill: #FFF;
      }
    }

    .btn-close {
      position: fixed;
      top: 1em;
      right: 1em;
      z-index: 30;
      cursor: pointer;

      @media screen and (prefers-reduced-motion: no-preference) and (update: fast) {
        transition: 0.2s scale ease-in-out;
      }
      
      &:hover {
        scale: 1.15;
      }
    }

    .btn-expand {
      align-items: center;
      justify-content: center;
      padding: 3px;
      position: absolute;
      top: 1em;
      right: 1em;
      z-index: 1;
      opacity: 0;
      
      @media screen and (prefers-reduced-motion: no-preference) and (update: fast) {
        transition: 0.3s opacity;
      }
    }

    &:hover .btn-expand {
      opacity: 1;
    }

    &.expanded {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      padding-bottom: 0 !important;
      background: rgba(0, 0, 0, 0.9);
      position: fixed !important;
      top: 0;
      left: 0;
      z-index: 999999;
      cursor: default;

      img {
        max-width: 95vw;
        max-height: 99vh;
        object-fit: contain;
        margin: 0 auto;
      }

      .title {
        width: 100%;
        font-size: 1.2em;
        border-radius: 0;
        bottom: 0;
      }
    }
  }
</style>
